$primary_color: #04ABFB;
$light_color: #04ABFB;
$dark_color: #03426E;

.background-image {
    position: relative;
    width: 100%;
    height: 300px; /* Adjust the height as needed */
    display: flex;
    align-items: center; /* Center the content vertically */
  
    /* Add this for the dimming effect */
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, .25); /* Adjust the alpha value for the desired dimness */
    }
  
    /* Add more styles if needed */
  }

  .nav-bar {
    background-color: $light_color;
  }